import React, { useState } from 'react';
import PropTypes from "prop-types";
import RangeTouch from 'rangetouch';
RangeTouch.setup('[type=range]');

/*
  data = [a, [b, b, b], [[c, c, c], [...], [...]]]
  data.length = sequence
  a, b, c, ...
    ba, cb
    
    childSize: (0<childSize<1)
  angle: 
*/
const makeFractal = () => {
  const polygon = parseInt(document.getElementById("polygonSlider").value);
  const sequence = parseInt(document.getElementById("sequenceSlider").value);
  const childSize = parseFloat(document.getElementById("childSizeSlider").value);
  //: deg
  const rotate = parseInt(document.getElementById("windowRotateSlider").value);
  //
  const childNumberElement = document.getElementById("childNumber");
  const childNumber = (childNumberElement !== null) ? parseInt(childNumberElement.textContent) : 1;
  //maxPolygon()
  const maxPolygon = 10000;

  //: 
  const totalPolygon = 1*(Math.pow(polygon*childNumber,sequence)-1)/(polygon*childNumber-1);
  console.log("polygon: "+totalPolygon);
  if(totalPolygon < maxPolygon){
    const canvas = document.getElementById("fractalCanvas");
    const ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    const root = {
      "x":canvas.width/2,
      "y":canvas.height/2,
      //d
      "d": canvas.width>canvas.height?canvas.height*0.25:canvas.width*0.25
    }
    //console.log(root)

    //(root)
    const makePolygon = (polygon=polygon,sequence=sequence,currentSequence,childSize=childSize,angle,param=root) => {
      const d = param.d;
      ctx.beginPath();
      //
      const condition = (document.getElementById("parentDeleteButton").textContent==="ON") ? (currentSequence>=sequence) : true;
      if(condition){
        for(let i=0;i<polygon;i++){
          const rad = 360*(Math.PI/180)*(i/polygon) + (-90+angle*(currentSequence-1)+rotate)*(Math.PI/180);
          const x = param.x + d*Math.cos(rad);
          const y = param.y + d*Math.sin(rad);
          if(i==0){
            ctx.moveTo(x,y);
          }else{
            ctx.lineTo(x,y);
          }
          //console.log("x:"+x+", y:"+y)
        }
        ctx.closePath();
        ctx.stroke();
      }
      //
      if(currentSequence < sequence){
        //j: (22), i: polygon
        for(let j=0;j<childNumber;j++){
          //(): deg
          const childAngleElement = document.getElementById("childRotateNumber_"+(j+1));
          const angle = (childAngleElement !== null) ? parseInt(childAngleElement.textContent) : 0;
          const childMoveDistanceElement = document.getElementById("childMoveDistanceNumber_"+(j+1));
          const childMoveDegreeElement = document.getElementById("childMoveDegreeNumber_"+(j+1));
          const moveD = (childMoveDistanceElement !== null) ? Number(childMoveDistanceElement.textContent)*Math.pow(childSize,currentSequence) : 0;
          for(let i=0;i<polygon;i++){
            //(x,y)(d)
            //
            const d = param.d*childSize;
            const commonRad = 360*(Math.PI/180)*(i/polygon) + (-90+angle*currentSequence+rotate)*(Math.PI/180);
            const moveRad = 360*(Math.PI/180)*(i/polygon) + (-90+((childMoveDegreeElement !== null) ? parseInt(childMoveDegreeElement.textContent) : 0))*(Math.PI/180);
            const x = param.x + (param.d-d)*Math.cos(commonRad) + moveD*Math.cos(moveRad);
            const y = param.y + (param.d-d)*Math.sin(commonRad) + moveD*Math.sin(moveRad);
            const parent = {"x":x, "y":y, "d":d};
            makePolygon(polygon,sequence,currentSequence+1,childSize,angle,parent);
          }
        }
      }
    };
    makePolygon(polygon,sequence,1,childSize,0,root);
  }else{
    console.log("Too many polygon. (max="+maxPolygon+")")
  }
}


const ChildParam = (props) => {
  const num = props.num;
  return (
    <table border="0">
      <tbody>
        <tr>
          <td>{""+num+"(deg): "}</td>
          <td>
            <input id={"childRotateSlider_"+num} type="range" min="0" max="360" step="1" defaultValue="0" onChange={()=>{
              //
              const value = document.getElementById("childRotateSlider_"+num).value;
              const element = document.getElementById("childRotateNumber_"+num);
              element.textContent = value+"";
              //
              makeFractal();
            }}/>
          </td>
          <td id={"childRotateNumber_"+num}>0</td>
        </tr>
        <tr>
          <td>{""+num+"(): "}</td>
          <td>
            <input id={"childMoveDistanceSlider_"+num} type="range" min="0" max={window.innerWidth>window.innerHeight?window.innerWidth*0.5:window.innerHeight*0.5} step="1" defaultValue="0" onChange={()=>{
              //
              const value = document.getElementById("childMoveDistanceSlider_"+num).value;
              const element = document.getElementById("childMoveDistanceNumber_"+num);
              element.textContent = value;
              //
              makeFractal();
            }}/>
          </td>
          <td id={"childMoveDistanceNumber_"+num}>0</td>
        </tr>
        <tr>
          <td>{""+num+"(deg): "}</td>
          <td>
            <input id={"childMoveDegreeSlider_"+num} type="range" min="0" max="360" step="1" defaultValue="0" onChange={()=>{
              //
              const value = document.getElementById("childMoveDegreeSlider_"+num).value;
              const element = document.getElementById("childMoveDegreeNumber_"+num);
              element.textContent = value+"";
              //
              makeFractal();
            }}/>
          </td>
          <td id={"childMoveDegreeNumber_"+num}>0</td>
        </tr>
      </tbody>
    </table>
  );
}
ChildParam.propTypes = {
  num: PropTypes.number,
};

const ChildsParam = (props) => {
  const childNumber = props.childNumber;
  const [element,setElement] = useState(<div><ChildParam num={1} key={1} /></div>);
  React.useEffect(() => {
    const childNumberArray = [...Array(childNumber).keys()].map(i => i+1);
    setElement (
      <div>
        {childNumberArray.map((num, key) => (
          <ChildParam num={num} key={key} />
        ))}
      </div>
    );
  }, [childNumber]);
  return element;
}
ChildsParam.propTypes = {
  childNumber: PropTypes.number,
};

const CommonParam = (props) => {
  const setChildNumber = props.setChildNumber;
  return (
    <table border="0">
      <tbody>
        <tr>
          <td>{": "}</td>
          <td>
            <input id="polygonSlider" type="range" min="2" max="10" step="1" defaultValue="3" onChange={()=>{
              //
              const value = document.getElementById("polygonSlider").value;
              const element = document.getElementById("polygonNumber");
              element.textContent = value;
              //
              makeFractal();
            }}/>
          </td>
          <td id="polygonNumber">3</td>
        </tr>
        <tr>
          <td>{": "}</td>
          <td>
            <input id="sequenceSlider" type="range" min="1" max="10" step="1" defaultValue="4" onChange={()=>{
              //
              const value = document.getElementById("sequenceSlider").value;
              const element = document.getElementById("sequenceNumber");
              element.textContent = value;
              //
              makeFractal();
            }}/>
          </td>
          <td id="sequenceNumber">4</td>
        </tr>
        <tr>
          <td>{"(): "}</td>
          <td>
            <input id="childSizeSlider" type="range" min="0" max="1" step="0.01" defaultValue="0.50" onChange={()=>{
              //
              const value = document.getElementById("childSizeSlider").value;
              const element = document.getElementById("childSizeNumber");
              element.textContent = value;
              //
              makeFractal();
            }}/>
          </td>
          <td id="childSizeNumber">0.5</td>
        </tr>
        <tr>
          <td>{"(deg): "}</td>
          <td>
            <input id="windowRotateSlider" type="range" min="0" max="360" step="1" defaultValue="0" onChange={()=>{
              //
              const value = document.getElementById("windowRotateSlider").value;
              const element = document.getElementById("windowRotateNumber");
              element.textContent = value+"";
              //
              makeFractal();
            }}/>
          </td>
          <td id="windowRotateNumber">0</td>
        </tr>
        <tr>
          <td>{": "}</td>
          <td>
            <button id="parentDeleteButton" onClick={()=>{
              const element = document.getElementById("parentDeleteButton");
              element.textContent = element.textContent==="OFF"?"ON":"OFF";
              //
              makeFractal();
            }}>ON</button>
          </td>
        </tr>
        <tr>
          <td>{": "}</td>
          <td>
            <button id="childDeleteButton" onClick={()=>{
              const element = document.getElementById("childNumber");
              const value = parseInt(element.textContent)-1;
              if(value >= 1){
                element.textContent = value;
                setChildNumber(value);
                //console.log(element.textContent);
                //
                makeFractal();
              }
            }}>-</button>
            <button id="childAppendButton" onClick={()=>{
              const element = document.getElementById("childNumber");
              const value = parseInt(element.textContent)+1;
              if(value <= 3){
                element.textContent = value;
                setChildNumber(value);
                //console.log(element.textContent);
                //
                makeFractal();
              }
            }}>+</button>
          </td>
          <td id="childNumber">1</td>
        </tr>
      </tbody>
    </table>
  );
}
CommonParam.propTypes = {
  setChildNumber: PropTypes.func,
};

const Fractal = () => {
  //(onChange)
  const style = {borderStyle: "solid"}
  return(
    <div align={"center"}>
      <canvas id="fractalCanvas" width={window.innerWidth*0.9} height={window.innerHeight*0.6} style={style}></canvas>
    </div>
  );
};


const App = () => {
  const [childNumber, setChildNumber] = useState(1);
  return (
    <div>
      <ul valign={"top"} style={{display:"inline-block"}}>
        <li valign={"top"} style={{display:"inline-block"}} ><CommonParam setChildNumber={setChildNumber} /></li>
        {/**/}
        <li valign={"top"} style={{display:"inline-block"}} ><ChildsParam childNumber={childNumber} /></li>
      </ul>
      <Fractal />
    </div>
  );
}

export default App;
